
@mixin font-face($name, $file, $weight: 400, $style: normal) {
  @font-face {
    font-family: "#{$name}";
    src: url('../assets/fonts/#{$file}.ttf') format('ttf'),
    url('../assets/fonts/#{$file}.woff2') format('woff2'),
    url('../assets/fonts/#{$file}.woff') format('woff'),
    url('../assets/fonts/#{$file}.eot') format('eot');
    font-weight: $weight;
    font-style: $style;
    font-display: swap;
  }
}
@include font-face('Gilroy', 'Gilroy-Light', 300);
@include font-face('Gilroy', 'Gilroy-Regular', 400);
@include font-face('Gilroy', 'Gilroy-Medium', 500);
@include font-face('Gilroy', 'Gilroy-SemiBold', 600);
@include font-face('Gilroy', 'Gilroy-Bold', 700);

