.main_screen {
  background: linear-gradient(13.74deg, #ffc149 8.28%, #ffed49 94.27%);
  margin-bottom: 45px;
  flex-grow: 1;
  @include sm {
    margin-bottom: 80px;
  }
  @include lg {
    margin-bottom: 122px;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  padding: 22px 0;
  align-items: center;
  @include sm {
    display: flex;
    align-items: center;
    padding: 22px 0;
  }

  &__logo {
    width: 73px;
    cursor: pointer;
    @include lg {
      width: 84px;
    }
  }

  &__logo-img {
    width: 100%;
  }

  & .menu-switcher {
    cursor: pointer;
    border: none;
    background: none;
    width: 21px;
    padding: 0;
    @include sm {
      display: none;
    }
  }
}

.menu {
  display: none;
  position: absolute;
  z-index: 9999;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.89);
  backdrop-filter: blur(35px);
  width: 100%;
  padding: 140px 21px 76px 21px;
  height: 100vh;
  &.show {
    overflow-y: scroll;
    display: block;
  }
  @include sm {
    height: initial;
    display: flex;
    background: transparent;
    position: relative;
    padding: initial;
    width: auto;
  }

  &__btn-close {
    background: none;
    padding: 0;
    border: none;
    position: absolute;
    top: 50px;
    right: 30px;
    width: 30px;
    cursor: pointer;
    @include sm {
      display: none;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    margin-bottom: 110px;
    @include sm {
      flex-direction: row;
      margin-bottom: 0;
      align-items: center;
      justify-content: flex-end;
    }
  }

  &__item {
    margin-bottom: 35px;
    @include sm {
      margin: 0 3px;
      &:last-child {
        margin-right: 15px;
      }
    }
  }

  &__item-link {
    cursor: pointer;
    display: flex;
    align-items: center;
    @include sm {
      border: 1.5px solid transparent;
      border-radius: 49px;
      justify-content: center;
      padding: 5px 10px;
      &:hover {
        border: 1.5px solid #000000;
      }
    }
    @include lg {
      padding: 10px 25px;
    }
  }

  &__item-icon {
    width: 33px;
    height: 33px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 25px;
    @include sm {
      display: none;
    }
  }

  &__item-icon img {
    object-fit: contain;
    width: 100%;
    height: 100%;
  }

  &__item-link .text {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 23px;
    line-height: 17px;
    width: 100%;
    color: #000000;
    @include sm {
      font-size: 14px;
      line-height: 94.5%;
    }
    @include lg {
      font-size: 24px;
    }
  }

  &__item-desctop-icon {
    display: none;
    @include sm {
      display: block;
      width: 14px;
      margin-left: 6px;
      margin-top: -2px;
    }
    @include lg {
      width: 18px;
      margin-left: 10px;
    }
  }
}

.menu-contacts {
  @include sm {
    display: none;
  }

  &__title {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 94.5%;
    color: #000000;
    margin-bottom: 25px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
  }

  &__item {
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-bottom: 23px;
  }

  &__item-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    margin-right: 30px;
  }

  &__item-icon img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &__item-text {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 23px;
    line-height: 94.5%;
    color: #000000;
  }
}

.menu-socials {
  @include sm {
    display: none;
  }

  &__list {
    display: flex;
    align-items: flex-start;
  }

  &__item {
    cursor: pointer;
    margin-right: 50px;
  }

  &__item:last-child {
    margin-right: 0;
  }
  &__item:last-child &__item-icon {
    width: 50px;
    height: 50px;
  }
  &__item-icon {
    width: 40px;
    height: 40px;
  }
}

.main {
  position: relative;
  @include sm {
    margin-top: 0;
  }

  &__content {
    @include sm {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      padding-top: 30px;
      margin-bottom: 50px;
    }
    @include lg {
      padding-top: 95px;
      flex-direction: row;
      justify-content: flex-start;
      margin-bottom: 75px;
    }
  }

  &__banner {
    position: absolute;
    z-index: 0;
    right: -20px;
    top: 37px;
    @include sm {
      width: 482px;
      position: absolute;
      right: -70px;
    }
    @include lg {
      width: auto;
      position: absolute;
      right: -157px;
      height: auto;
    }
  }

  &__banner-image {
    height: 244px;
    margin-bottom: 11px;
    @include sm {
      width: 100%;
      height: auto;
      margin-bottom: 0;
    }
    @include lg {
      height: 668px;
    }
  }

  &__text {
    padding: 5px 0 22px;
    width: 100%;
    position: relative;
    z-index: 10;
    @include sm {
      padding: 0;
      padding-top: 80px;
      width: 50%;
    }
    @include lg {
      padding: 0;
      width: 60%;
      margin-bottom: 192px;
    }
  }
  &__welcome {
    display: flex;
    justify-content: center;
    width: 100%;
    @include sm {
      width: 50%;
    }
    @include lg {
      width: 40%;
    }
  }
  &__title {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 26px;
    line-height: 101.5%;
    color: #ffffff;
    margin-bottom: 20px;
    @include sm {
      font-size: 48px;
      line-height: 92%;
      margin-bottom: 45px;
    }
    @include lg {
      font-size: 93px;
      line-height: 92%;
    }
  }

  &__subtitle {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 145%;
    color: #000000;
    margin-bottom: 25px;
    @include sm {
      font-size: 14px;
      line-height: 131%;
      max-width: 286px;
      margin-bottom: 0;
    }
    @include lg {
      font-size: 25px;
      line-height: 131%;
      max-width: 488px;
    }
  }
}

.offers {
  margin-bottom: 32px;
  @include sm {
    padding: 0;
    margin-bottom: 50px;
  }
  @include lg {
    margin-bottom: 90px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    @include sm {
      flex-direction: row;
    }
  }

  &__item {
    display: flex;
    @include sm {
      flex-direction: column;
      width: 151px;
      margin-right: 40px;
      &:last-child {
        margin-right: 0;
        width: 175px;
      }
    }
    @include lg {
      margin-right: 90px;
      width: 212px;
    }
  }

  &__item-image {
    width: 95px;
    @include sm {
      margin-bottom: 20px;
      width: 110px;
      height: 130px;
    }
    @include lg {
      width: 140px;
      height: 160px;
      margin-bottom: 35px;
    }
  }

  &__item-image img {
    max-width: 75px;
    max-height: 80px;
    @include sm {
      width: 100%;
      height: 100%;
      max-width: 110px;
      max-height: 130px;
    }
    @include lg {
      width: 100%;
      height: 100%;
      max-width: 140px;
      max-height: 160px;
    }
  }

  &__item-content {
    margin-top: 26px;
    width: calc(100% - 95px);
    @include sm {
      width: 100%;
      margin-top: 0;
    }
  }

  &__item-title {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 19px;
    line-height: 1.1;
    color: #000000;
    @include sm {
      margin-bottom: 25px;
      text-align: center;
    }
    @include lg {
      font-size: 29px;
      margin-bottom: 40px;
    }
  }

  &__item-text {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 145%;
    color: #000000;
    @include sm {
      font-size: 12px;
    }
    @include lg {
      font-size: 16px;
    }
  }
}

.benefit-section {
  margin-bottom: 15px;
  @include sm {
    margin-bottom: 130px;
  }
  @include lg {
    margin-bottom: 148px;
  }
}

.benefits {
  &__title {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 101.5%;
    color: #000000;
    margin-bottom: 45px;
    @include sm {
      width: 376px;
      font-size: 36px;
      line-height: 95%;
    }
    @include lg {
      width: 664px;
      font-size: 63px;
      margin-bottom: 83px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    @include sm {
      flex-direction: row;
      margin: 0 -7px;
    }
    @include lg {
      justify-content: space-between;
      margin: 0;
    }
  }

  &__item {
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    @include sm {
      width: calc(100% / 4 - 7px);
      margin: 0 7px;
      flex-direction: column;
    }
    @include lg {
      width: calc(100% / 4 - 50px);
    }
  }

  &__item-image {
    width: 70px;
    height: 62px;
    margin-right: 25px;
    @include sm {
      height: 90px;
      width: auto;
      margin: 0 auto 10px;
    }
    @include lg {
      width: 178px;
      height: 155px;
      margin-bottom: 45px;
    }
  }

  &__item-image img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &__item-title {
    width: calc(100% - 95px);
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 145%;
    color: #000000;
    @include sm {
      width: 100%;
      text-align: center;
    }
    @include lg {
      font-size: 16px;
    }
  }
}

.join-section {
  margin-bottom: 20px;
  @include sm {
    margin-bottom: 40px;
  }
  @include lg {
    margin-bottom: 100px;
  }

  &__title {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 95%;
    color: #000000;
    margin-bottom: 20px;
    @include sm {
      font-size: 36px;
      max-width: 586px;
      margin-bottom: 35px;
    }
    @include lg {
      font-size: 63px;
      max-width: 1050px;
    }
  }

  &__content {
    @include sm {
      display: flex;
      background: #fffbd5;
      padding: 32px 57px;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__form {
    background: #fffbd5;
    padding: 50px 11px 30px;
    @include sm {
      width: 50%;
      padding: 0;
      max-width: 241px;
    }
    @include lg {
      max-width: 362px;
    }
  }

  &__image {
    display: none;
    @include sm {
      width: 50%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding-right: 32px;
    }
    @include lg {
      width: calc(100% - 362px);
      padding-right: 141px;
    }
  }

  &__image img {
    @include sm {
      width: 205px;
    }
    @include lg {
      width: 348px;
    }
  }
}

.sign-in {
  margin-bottom: 30px;

  &__list {
    display: flex;
    justify-content: center;
  }

  &__item {
    margin-right: 18px;
    border: 1.5px solid #000000;
    border-radius: 50px;
    width: 100px;
    height: 37px;
    display: flex;
    align-items: center;
    padding: 0 8px;
    cursor: pointer;
    @include lg {
      width: 164px;
      height: 61px;
    }

    &:hover {
      background-color: #ff9a00;
    }
    &:hover &-text {
      color: #ffffff;
    }
  }

  &__item:last-child {
    margin-right: 0;
  }

  &__item-icon {
    width: 23px;
    height: 23px;
    margin-right: 15px;
    @include lg {
      width: 41px;
      height: 41px;
      margin-right: 30px;
    }
  }

  &__item-text {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 11px;
    line-height: 13px;
    color: #000000;
    @include lg {
      font-size: 14px;
      line-height: 16px;
    }
  }
}

.form {
  &__body {
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 30px;
    }
  }

  &__footer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__input {
    width: 100%;
    background: white;
    border: none;
    border-radius: 0;
    padding: 10px 35px 10px 45px;
    margin-bottom: 10px;

    &:hover,
    &:focus,
    &:focus-visible {
      border: none;
      outline: none;
    }
    @include lg {
      font-size: 19px;
      padding: 21px 50px 17px 65px;
      margin-bottom: 15px;
    }
  }

  &__controls {
    width: 100%;
    position: relative;

    &:before {
      display: block;
      position: absolute;
      width: 18px;
      height: 18px;
      background-position: center center;
      background-size: contain;
      background-repeat: no-repeat;
      top: 11px;
      left: 15px;
      @include lg {
        width: 30px;
        height: 30px;
        left: 15px;
        top: 15px;
      }
    }

    &-eyes {
      cursor: pointer;
      position: absolute;
      top: 16px;
      right: 7px;
      width: 15px;
      height: 10px;
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center center;
      background-image: url('../assets/icons/eye.svg');
      &:hover {
        background-image: url('../assets/icons/eye_close.svg');
      }
      &--hide {
        background-image: url('../assets/icons/eye_close.svg');
      }
      @include lg {
        right: 20px;
        top: 22px;
        width: 25px;
        height: 17px;
      }
    }
    &--email {
      &:before {
        content: '';
        background-image: url('../assets/icons/user icon.svg');
      }
    }

    &--password {
      &:before {
        content: '';
        background-image: url('../assets/icons/lock.svg');
      }
    }

    &--promo {
      margin-bottom: 15px;
      &:before {
        content: '';
        background-image: url('../assets/icons/percent_color.svg');
      }
    }
  }
  &__controls-switcher {
    display: flex;
    justify-content: space-between;
    padding: 0 12px;
    margin: 5px 0;
    @include sm {
      padding: 0;
    }
    @include lg {
      padding: 0 15px;
    }
  }
}

.promo-label {
  margin-top: 17px;
  display: flex;
  margin-bottom: 24px;
  padding: 0 7px 0 17px;
  @include lg {
    padding: 0 15px;
  }

  &__icon {
    width: 20px;
    height: 20px;
    margin-right: 13px;
    @include lg {
      width: 30px;
      height: 30px;
    }
  }

  &__text {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 9px;
    line-height: 127.5%;
    color: #000000;
    @include sm {
      font-size: 11px;
    }
    @include lg {
      font-size: 13px;
    }
  }
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 43px;
  font-family: 'Gilroy', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  color: #000000;
  max-width: 182px;
  width: 100%;
  height: 40px;
  border: none;
  cursor: pointer;
  margin-bottom: 17px;

  &-primary {
    background: #ff9a00;

    &:hover {
      background: black;
      color: white;
    }
  }

  @include sm {
    margin-bottom: 11px;
    max-width: 145px;
    height: 30px;
  }
  @include lg {
    max-width: 246px;
    height: 52px;
    font-size: 18px;
    line-height: 143.5%;
  }
}

.link {
  font-family: 'Gilroy', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 143.5%;
  text-align: center;
  color: #000000;
  margin-bottom: 20px;
  @include sm {
    font-size: 14px;
    line-height: 143.5%;
    margin-bottom: 16px;
  }
  &:hover {
    color: #ff9a00;
  }

  &--underline {
    margin-bottom: 0;
    text-decoration: underline;
  }

  &--small {
    font-size: 9px;
    line-height: 96.5%;
    @include sm {
      font-size: 13px;
      line-height: 96.5%;
    }
  }
}

.footer {
  background: linear-gradient(244.92deg, #02c2ff 24.54%, #0289ff 85.62%),
    #0289ff;
  padding: 42px 0 60px;
  @include sm {
    padding: 45px 12px 102px 12px;
  }
  @include lg {
    padding: 80px 40px 120px 40px;
  }

  &-menu {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    @include sm {
      margin-bottom: 0;
    }
  }

  &-menu__title {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 94.5%;
    color: #ffffff;
    margin-bottom: 15px;
    @include lg {
      margin-bottom: 23px;
      font-size: 24px;
    }
    @media (max-width: 576px) {
      font-size: 16px;
      margin-bottom: 21px;
    }
  }

  &-menu__list {
    column-count: 1;
    column-gap: 45px;
    white-space: nowrap;
    max-width: 400px;
  }

  @media screen and (max-width: 960px) {
    &-menu__list {
      white-space: inherit;
      max-width: 90%;
    }
  }

  @media screen and (max-width: 767px) {
    &-menu__list {
      max-width: 400px;
    }
    &-menu {
      margin-bottom: 0;
    }
  }

  &-menu__item {
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  &-menu__item a {
    cursor: pointer;
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    color: #ffffff;
    @include lg {
      font-size: 18px;
    }
  }

  &-contacts,
  &-social {
    margin-top: 35px;
    @include sm {
      margin-top: 0;
    }
  }

  &-contacts__title {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 94.5%;
    color: #ffffff;
    margin-bottom: 20px;
    @include lg {
      font-size: 24px;
    }
  }

  &-contacts__list {
    display: flex;
    flex-direction: column;
  }

  &-contacts__item {
    margin-bottom: 25px;
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &-contacts__item-icon {
    width: 18px;
    margin-right: 16px;
    @include lg {
      width: 30px;
      height: 30px;
    }
  }

  &-contacts__item-text {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 94.5%;
    color: #ffffff;
    @include lg {
      font-size: 18px;
    }
  }

  &-social {
    height: 100%;
    margin-right: 50px;
    @include lg {
      margin-right: 20px;
    }
  }

  &-social__list {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    @include sm {
      flex-direction: row;
      align-items: flex-start;
      justify-content: flex-end;
    }
    @include lg {
      margin-top: 40px;
      justify-content: flex-start;
    }
  }

  &-social__item {
    margin-bottom: 15px;
    @include sm {
      margin-bottom: 0;
      margin-right: 15px;
      &:last-child {
        margin-right: 0;
      }
    }
  }

  &-social__item:last-child {
    margin-bottom: 0;
  }

  &-social__item-icon {
    width: 30px;
    height: 30px;
    @include lg {
      width: 55px;
      height: 35px;
    }
  }

  & hr {
    background: #ffffff;
    border: none;
    box-shadow: none;
    height: 1px;
    margin: 5px 10px 25px;

    @include sm {
      margin: 15px 20px 25px;
    }
    @include lg {
      margin: 40px 20px 30px;
    }
  }

  &-offers {
    @include sm {
      display: flex;
      justify-content: center;
