  width: 100%;
  height: 100%;
  display: flex;

  &-card {
    position: absolute;
    width: 10px;
    height: 10px;
    top: 13px;

    &--left {
      left: 2px;
    }

    &--right {
      right: 2px;
    }
  }

  &--left {
    transform: rotate(90deg);
  }
  &--right {
    transform: rotate(-90deg);
  }
}

.alert {
  margin-bottom: 36px;
  position: relative;
  width: 100%;
  right: 0;
  bottom: 0;
  z-index: 999;
  box-shadow: 4px 4px 26px rgba(0, 0, 0, 0.07);
  border-radius: 20px;
  width: 535px;
  flex: 1;
  min-height: 120px;
  padding: 20px 38px 37px;
  user-select: none;
  @media (max-width: 576px) {
    width: 100%;
  }
  &-container {
    position: fixed;
    display: flex;
    flex-direction: column;
    bottom: 0;
    right: 0;
    z-index: 999;
    margin-right: 36px;
    @media (max-width: 576px) {
      margin: 0 20px;
      left: 0;
    }
  }

  &-inner {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  &.success {
    background-color: #c5f7dd;
    color: #2c7c4f;
    path {
      stroke: #2c7c4f;
    }
  }
  &.error {
    background-color: #ffd0cb;
    color: #bb392c;
    path {
      stroke: #bb392c;
    }
  }
  &__close {
    cursor: pointer;
    position: absolute;
    right: 20px;
    top: 20px;
    width: 15px;
    height: 15px;
  }
  &__title {
    font-weight: 500;
    font-size: 22px;
    line-height: 27px;
    margin-bottom: 7px;
    text-transform: capitalize;
  }
  &__desc {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    max-width: 336px;
    width: 100%;
  }
}
