html,
body {
  height: 100%;
  font-family: 'Gilroy', sans-serif;
  overflow-x: hidden;
}
.no-overflow {
  overflow: hidden;
}
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: 100%;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1164px;
  margin: 0 auto;
  padding: 0 20px;
  @include sm {
    padding: 0 38px;
  }
  @include lg {
    max-width: 1264px;
  }
}

.block {
  display: block;
}
.text-center {
  text-align: center;
}
.row-xs {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.cols-2 {
  width: 100%;
  @include sm {
    width: 50%;
  }
  @include lg {
    width: calc(50% - 78px);
    margin-right: 156px;
    &:last-child {
      justify-content: flex-end;
      margin-right: 0;
    }
  }
}
.cols-xs-2 {
  width: 50%;
}

@media screen and (max-width: 960px) and (min-width: 767px) {
  .footer .cols-xs-2 {
    padding: 0 10px;
  }

  .footer .row {
    margin: 0 -10px;
  }
}

.switch {
  position: relative;
  display: inline-block;
  width: 31px;
  height: 18px;
  &__text {
    font-family: 'Gilroy', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 127.5%;
    color: #000000;
    width: calc(100% - 45px);
    line-height: 18px;
    @include lg {
      font-size: 13px;
    }
    a {
      color: inherit;
    }
  }
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: transparent;
  border: 1px solid #000000;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: '';
  height: 14px;
  width: 14px;
  left: 2px;
  bottom: 1px;
  background-color: #000000;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider:before {
  background-color: #ff9a00;
}

input:checked + .slider:before {
  -webkit-transform: translateX(11px);
  -ms-transform: translateX(11px);
  transform: translateX(11px);
}
