$blue: #1673ff;

input[type='text'],
input[type='button'],
button {
  appearance: none;
}

.lk {
  padding-top: 50px;

  &__tabs {
    padding: 0 40px !important;
    margin: 0;
    display: flex;
    align-items: flex-end;

    li {
      flex-grow: 1;
    }
  }

  &__tab {
    background: rgba(#ffffff, 0.5);
    border-radius: 20px 20px 0 0;
    padding: 18px 20px 11px;
    font-size: 16px;
    line-height: 130.7%;
    text-align: center;
    color: #7d7d7d;
    margin-right: 5px;
    display: block;

    &--active,
    &:hover {
      background: #fff;
      color: #000000;
    }

    &:hover {
      cursor: pointer;
    }
  }

  &__block {
    padding: 50px;
    background: #ffffff;
    border-radius: 30px;

    &-submit {
      max-width: 285px;
    }

    &--small {
      padding: 50px 20px;
    }

    &--border {
      border-radius: 30px 30px 0 0;
    }

    &--choose {
      padding-top: 60px;
      padding-bottom: 96px;
    }

    &--profile {}
  }

  &__empty {
    min-height: 450px;
    margin-bottom: 10px;
  }

  &__title {
    font-weight: 500;
    font-size: 22px;
    line-height: 120.7%;
  }

  &__icon {
    margin-left: 16px;
  }

  &__desc {
    font-weight: 500;
    font-size: 16px;
    line-height: 143.2%;
    color: #6c6c6c;
    margin-top: 23px;

    a {
      color: $blue;
      text-decoration: underline;

      &:hover {
        text-decoration: none;
      }
    }
  }

  &__mob {
    &-toggle {
      display: none;
    }
  }
}

.lk-card {
  &:not(:last-child) {
    margin-bottom: 10px;
  }

  &__title {
    margin-bottom: 44px;
  }

  &__wrap {
    display: grid;
    grid-template-columns: 284px calc(100% - 300px);
    gap: 16px;
  }

  &__img {
    img {
      width: 100%;
      display: block;
    }

    @media (max-width: 576px) {
      margin: 0 auto 7px;
    }
  }

  &__content {
    max-width: 650px;
  }

  &__fields {
    display: flex;
    flex-wrap: wrap;
  }

  &__info {
    border: 1px solid #c2c2c2;
    border-radius: 20px;
    padding: 15px 20px 13px;
    min-height: 100%;
    margin: 0 15px 10px 0;
    width: calc(50% - 30px);
    position: relative;

    &--phone {
      @media (max-width: 576px) {
        background: #f8f6f0;
        border: 0.508457px solid #ffffff;
        padding: 12px 14px 11px !important;
      }
    }

    &-title {
      font-weight: 500;
      font-size: 16px;
      line-height: 143.2%;
      color: #6c6c6c;
    }

    &-input,
    &-desc {
      font-weight: 500;
      font-size: 22px;
      line-height: 120.7%;
      margin-top: 6px;
      display: block;
    }

    &-input {
      border: none;
      padding: 0 20px 0 0;
      outline: none !important;
      pointer-events: none;
      background-color: transparent;

      &--active {
        pointer-events: unset;
      }
    }

    &-btn {
      position: absolute;
      right: 21px;
      bottom: 19px;
      padding: 0;
      border: none;
      background-color: transparent;
      z-index: 2;

      &:hover {
        cursor: pointer;

        path {
          stroke: $blue;
        }
      }
    }

    &--editable {
      background: #f8f6f0;
      border-color: #fff;
      position: relative;
    }

    &.small {
      width: calc(25% - 25px);
    }
  }

  &__link {
    display: inline-block;
    margin-top: 24px;
    font-weight: 500;
    font-size: 16px;
    line-height: 143.2%;
    color: $blue;
    text-decoration-line: underline;

    &:hover {
      text-decoration: none;
    }
  }
}

.lk-step {
  &:not(:last-child) {
    margin-bottom: 1px;
  }

  &__title {
    display: flex;
    align-items: center;

    i {
      font-style: normal;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      width: 44px;
      height: 44px;
      background: #ffe949;
      margin-right: 13px;
      flex-shrink: 0;
    }
  }

  &__balance {
    margin-top: 11px;
    display: flex;
    flex-wrap: wrap;

    &-input {
      margin: 10px 10px 0 0;
      position: relative;
      font-size: 13px !important;

      svg {
        position: absolute;
        top: -8px;
        right: -8px;
      }

      input {
        margin: 0;
        opacity: 0;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        cursor: pointer;

        &:checked+label {
          border-color: transparent;

          &::before {
            opacity: 1;
          }
        }
      }

      label {
        padding: 0 27px;
        border: 0.5px solid #000000;
        border-radius: 20px;
        height: 60px;
        display: flex;
        align-items: center;
        font-weight: 600;
        font-size: 22px;
        line-height: 145%;

        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          opacity: 0;
          border: 1.25328px solid #1c32ff;
          border-radius: 20px;
        }
      }

      &--big {
        label {
          padding: 0 48px;
        }
      }
    }
  }

  &__fields {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 16px);
    margin-top: 27px;

    &.border-top {
      padding-top: 35px;
      border-top: 1px solid #c2c2c2;
    }

    @media (max-width: 576px) {
      margin-top: 0;
    }

    .fg {
      width: 285px;
      max-width: 100%;
      margin-right: 16px;
    }
  }
}

.fg {
  width: 100%;
  padding: 0;
  margin: 0;
  border: none;
  position: relative;

  .copy {
    margin-left: 5px;
    margin-right: 5px;
  }

  &--editable {
    input {
      pointer-events: none;
    }
  }

  label {
    display: block;
    font-weight: 500;
    font-size: 16px;
    line-height: 143.2%;
    color: #6c6c6c;
    padding-left: 21px;
    margin-bottom: 3px;

    @media (max-width: 576px) {
      padding-left: 0;
      font-size: 14px;
    }
  }

  input {
    width: 100%;
    background: #f8f6f0;
    border: 0.508457px solid #ffffff;
    border-radius: 20px;
    height: 70px;
    padding: 0 45px 0 21px;
    font-weight: 500;
    font-size: 22px;
    line-height: 120.7%;
    color: #000;
    outline: none !important;

    &::placeholder {
      color: #000;
    }
  }

  .input--active {
    pointer-events: unset;
  }

  &__edit {
    height: 70px;
    display: flex;
    align-items: center;
    position: absolute;
    right: 16px;
    bottom: 0;
    padding: 0;
    border: none;
    background-color: transparent;
    z-index: 2;

    &:hover {
      cursor: pointer;

      path {
        stroke: $blue;
      }
    }
  }

  &__hint {
    position: absolute;
    top: 100%;
    left: 0;
    display: block;
    width: 100%;
    text-align: center;
    font-size: 16px;
    line-height: 130.7%;
    color: #747474;
    margin-top: 10px;
  }

  &--bg {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    @media (max-width: 576px) {
      .copy-btn {
        bottom: -1px;
        right: -3px;
      }
    }

    .input {
      padding: 0;
      display: flex;
      align-items: flex-end;

      border-radius: 20px;
      height: 50px;
      font-weight: 500;
      font-size: 22px;
      line-height: 120.7%;
      color: #000;

      @media (max-width: 576px) {
        height: 40px;
        font-size: 16px;
      }
    }
  }
}

.lk-currency {
  max-width: 585px;
  margin-top: 25px;

  &__tabs {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px;
    margin-bottom: 65px;

    @media (max-width: 576px) {
      margin-bottom: 0;
    }
  }

  &__tab {
    text-align: center;
    border: 0.508457px solid #c2c2c2;
    border-radius: 20px;
    min-height: 140px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;

    &:hover {
      cursor: pointer;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      border: 2px solid #1c32ff;
      border-radius: 20px;
      transition: all 0.3s;
    }

    &:hover,
    &--active {
      border-color: transparent;

      &::before {
        opacity: 1;
      }
    }

    &[data-label]:after {
      content: attr(data-label);
      position: absolute;
      left: 0;
      bottom: 100%;
      display: block;
      font-weight: 500;
      font-size: 16px;
      line-height: 143.2%;
      color: #6c6c6c;
      margin-bottom: 10px;
    }

    &-icon {
      height: 53px;
    }

    img {
      max-height: 100%;
      max-width: 100%;
    }

    &-title {
      font-weight: 500;
      font-size: 16px;
      line-height: 143.2%;
      color: #6c6c6c;
      display: block;
      margin-top: 21px;
    }
  }

  &__content {
    margin-top: 16px;
    border: 0.508457px solid #c2c2c2;
    border-radius: 20px;
    padding: 30px 50px;

    &--active {
      display: block;
    }
  }

  &__wallet {
    border: 0.508457px solid #c2c2c2;
    border-radius: 20px;
    padding: 24px 55px;
    text-align: center;
    font-weight: 500;
    font-size: 16px;
    line-height: 143.2%;
    color: #6c6c6c;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    position: relative;
    cursor: pointer;

    .copy-btn {
      position: absolute;
      right: 23px;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
      margin-top: -4px;

      @media (max-width: 992px) {
        margin-top: 2px;
      }

      svg {
        height: 16px;
      }
    }
  }

  &__qr {
    display: block;
    width: 165px;
    margin: 23px auto 0;

    @media (max-width: 576px) {
      width: 125px;
    }
  }

  &__desc {
    margin-top: 12px;
    text-align: center;

    @media (max-width: 576px) {
      margin-top: 5px;
    }
  }

  &__balance {
    margin: 21px auto 27px;
    max-width: 283px;
    position: relative;

    .copy-btn {
      bottom: 7px;

      svg {
        height: 16px;
      }
    }

    .lk-card__info-desc {
      margin-top: 4px;
      font-size: 28px;
      word-break: break-word;
      padding-right: 10px;
      overflow: hidden;

      @media (max-width: 576px) {
        font-size: 16px;
      }
    }
  }

  &__btn {
    background: #ffbf73;
    border-radius: 25px;
    margin: 10px auto;
    width: 100%;
    max-width: 350px;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 22px;
    border: none;
    transition: 0.3s;
    color: #000;

    &.left {
      margin: 10px 0;
    }

    &:hover {
      cursor: pointer;
      background: darken(#ffbf73, 10%);
    }

    &:disabled {
      background: #cccccc;
      cursor: not-allowed;
    }

    &--danger {

      // background: #ca3d3d;
      &:hover {
        background: #ca3d3d;
        color: #fff;
      }
    }

    &--link {
      background: transparent;

      &:hover {
        background: #ca3d3d;
      }
    }
  }
}

.bank-card {
  &__fields {
    display: flex;
    flex-wrap: wrap;

    .fg {
      margin-bottom: 10px;

      input {
        height: 64px;
      }
    }
  }

  &__cvv,
  &__year,
  &__month {
    max-width: 135px;
  }

  &__cvv {
    margin-left: auto;
  }

  &__month {
    margin-right: 15px;

    &::after {
      content: '/';
      width: 15px;
      height: 64px;
      position: absolute;
      left: 100%;
      bottom: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 600;
      font-size: 16px;
    }
  }
}

.lk-profile {
  min-height: 430px;
}

.lk-stat {
  &__balance {
    padding: 20px 30px;
    display: inline-flex;
    align-items: center;

    .lk-card__info-desc {
      margin: 0 0 0 23px;
      font-size: 28px;
    }
  }

  &__title {
    margin-top: 35px;
    margin-bottom: 15px;
  }

  &__table {
    overflow-x: auto;
    width: calc(100% + 20px);
    margin: 0 0 0 -10px;

    table {
      width: 100%;
      min-width: 1000px;
      border-collapse: collapse;
    }

    tbody {
      tr {
        &:nth-child(odd) {
          background: rgba(#f1f2f2, 0.5);
        }
      }
    }

    th {
      font-weight: 500;
      font-size: 16px;
      line-height: 143.2%;
      color: #6c6c6c;
      padding: 11px 0;
      text-align: left;
    }

    th,
    td {
      &:nth-child(1) {
        padding-left: 36px;
      }

      &:nth-child(3) {
        text-align: right;
      }

      &:last-child {
        padding-right: 36px;
        text-align: right;
      }
    }

    td {
      height: 60px;
      font-weight: 500;
      font-size: 16px;
      line-height: 95%;
      letter-spacing: 0.02em;
      color: #171717;
    }
  }

  &__descr {}
}

@media screen and (max-width: 1100px) {
  .lk {
    &__block {
      padding: 35px;
    }
  }

  .lk-card {
    &__info {
      padding-left: 20px;
      padding-right: 20px;
    }
  }
}

@media screen and (max-width: 1070px) {
  .lk {
    &__tabs {
      padding: 0 0 0 20px !important;
    }

    &__tab {
      padding: 10px 20px;
      font-size: 14px;
      border-radius: 10px 10px 0 0;
    }

    &__block {
      border-radius: 20px;
      padding: 25px;

      &--choose {
        padding: 50px 25px 80px;
      }
    }

    &__empty {
      min-height: 300px;
    }

    &-card {
      &__content {
        max-width: none;
      }
    }
  }

  .lk-card {
    &__title {
      margin-bottom: 30px;
    }

    &__wrap {
      grid-template-columns: 100%;
    }

    &__img {
      max-width: 250px;
    }
  }

  .lk-step {
    &__balance {
      &-input {
        svg {
          width: 24px;
          height: auto;
        }

        label {
          height: 40px;
          border-radius: 10px;
          padding: 0 15px;
          font-size: 20px;

          &::before {
            border-radius: 10px;
          }
        }

        &--big {
          label {
            padding: 0 25px;
          }
        }
      }
    }

    &__fields {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 16px;
      margin-bottom: 15px;

      @media (max-width: 576px) {
        margin-bottom: 0;
      }

      .fg {
        width: 100%;
        margin: 0;
        margin-bottom: 4px;

        @media (max-width: 576px) {
          .field {
            height: 60px;
            padding: 0 15px;
            padding-right: 43px;
          }
        }
      }
    }
  }

  .lk-profile {
    min-height: 300px;
  }
}

@media screen and (max-width: 767px) {
  .lk {
    padding-top: 25px;

    &__tabs {
      margin-bottom: 10px;
      flex-wrap: wrap;
      padding: 0 !important;

      li {
        margin-bottom: 5px;
        flex-shrink: 0;
      }
    }

    &__tab {
      border-radius: 10px;
    }

    &__title {
      font-size: 20px;

      @media (max-width: 576px) {
        font-size: 18px;
      }
    }

    &__desc {
      font-size: 14px;
    }

    &__block {
      padding: 20px 20px;

      &--small-pad {
        @media (max-width: 576px) {
          padding: 0px 0px 30px;
        }
      }

      &--choose {
        padding: 50px 25px 70px;

        @media (max-width: 576px) {
          padding: 30px 25px 33px;
        }
      }

      &--profile {
        padding: 27px 15px 13px;
      }
    }

    &__empty {
      min-height: 200px;
    }
  }

  .lk-card {
    &__fields {
      grid-template-columns: 100%;
    }

    &__info {
      width: 100%;
      margin-right: 0;
      border-radius: 16px;
      padding: 14px 14px 13px;

      @media (max-width: 576px) {
        margin-bottom: 15px;
      }

      &--mrn {
        margin-right: auto;

        @media (max-width: 576px) {
          width: 172px;
        }
      }

      &--desc {
        @media (max-width: 576px) {
          // padding: 11px 14px 8px;
          display: flex;
          max-width: 172px;

          .lk-card__info-title {
            width: 70px;
            font-size: 14px;
            line-height: 143.2%;
          }

          .lk-card__info-desc {
            font-weight: 500;
            font-size: 24px;
            line-height: 120.7%;
          }
        }
      }

      &-title {
        line-height: 1.1;
        font-size: 14px;

        @media (max-width: 350px) {
          display: inline-block;
        }
      }

      &-desc {
        font-size: 20px;
      }

      &.middle {
        min-width: 172px;
        flex: 1;

        @media (max-width: 480px) {
          min-width: 150px;
        }
      }

      &.small {
        width: calc(50% - 8px);
        flex: 1;

        @media (max-width: 374px) {
          max-width: 120px;
        }
      }
    }

    &__link {
      font-size: 14px;
      margin: 13px 0;
    }
  }

  .lk-step {
    &__title {
      i {
        width: 37px;
        height: 37px;
        margin-right: 10px;

        @media (max-width: 576px) {
          width: 30px;
          height: 30px;
          font-weight: 500;
          font-size: 14px;
        }
      }
    }

    &__fields {
      grid-template-columns: 1fr;
    }
  }

  .lk-currency {
    margin-top: 20px;

    &__tabs {
      overflow-x: auto;
      display: grid;
      grid-template-columns: repeat(3, calc(33.33% - 2px));
      gap: 3px;
      margin-bottom: 20px;
    }

    &__tab {
      flex-shrink: 0;
      padding: 5px;
      border-radius: 10px;
      min-height: 100px;

      &::before {
        border-radius: 10px;
      }

      &[data-label]::after,
      &-title {
        font-size: 10px;
      }

      &-title {
        margin-top: 10px;
      }

      &-icon {
        height: 45px;
      }
    }

    &__content {
      border-radius: 20px;
      padding: 16px;
    }

    &__wallet {
      padding: 17px 32px;
      border-radius: 16px;

      .copy-btn {
        right: 15px;
      }
    }

    &__btn {
      height: 60px;
      font-size: 18px;
      border-radius: 20px;
      margin-top: 15px;
    }
  }

  .bank-card {

    &__month,
    &__year {
      max-width: calc(50% - 7.5px);
    }

    &__cvv {
      max-width: 100%;
    }

    &__fields {
      .fg {
        input {
          height: 55px;
        }
      }
    }
  }

  .lk-profile {
    min-height: 200px;

    .lk-step__fields {
      margin-top: 0;
    }
  }

  .lk-stat {
    &__balance {
      display: block;
      padding: 15px 20px 13px;

      .lk-card__info-desc {
        margin: 5px 0 0;
        font-size: 22px;
      }
    }

    &__title {
      padding-left: 0;
    }

    &__table {
      width: calc(100% + 40px);
      margin-left: -20px;

      table {
        min-width: unset;
      }

      table,
      tbody,
      tr {
        display: block;
      }

      tr {
        padding: 15px 0;
      }

      td {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 12px 20px !important;
        text-align: right !important;
        font-size: 14px;
        line-height: 120%;
        height: unset;

        &::before {
          content: attr(data-label);
          width: 50%;
          flex-shrink: 0;
          padding-right: 5px;
          text-align: left !important;
        }
      }

      thead {
        display: none;
      }
    }
  }

  .fg {
    input {
      font-size: 16px;
      height: 50px;

      @media (max-width: 576px) {
        border: none;
        padding: 0 43px 0 0px;
        height: 25px;
      }
    }

    &__edit {
      height: 60px;
    }

    &__hint {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 520px) {
  .lk__block-submit {
    max-width: 100%;
  }

  .lk-card {
    &__title {
      font-size: 14px;
      font-weight: 600;
    }
  }
}

.lk-card__img img,
img.card {
  border-radius: 15px;
}

