    @media (max-width: 992px) {
      display: block;
    }
  }

  &-header {
    position: relative;
    padding: 5px 15px;
    width: 100%;
    height: 40px;
    transition: 200ms ease-in-out;
    font-weight: 500;
    font-size: 24px;
    line-height: 20px;
    color: #2a0039;
    box-sizing: border-box;
    height: 60px;
    background: #f8f6f0;
    border-radius: 20px;

    &:focus,
    &:focus-visible {
      outline: none;
      border-color: #c2c2c2;

      span {
        &::before {
          color: #caa5ec;

          @media (max-width: 992px) {
