  }

  &-content {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding-right: 20px;

    span {
      margin-top: 16px;
    }

    @media (max-width: 992px) {
      outline: none;
    }
  }

  &-arrow {
    margin-right: 3px;
    transition: 0.3s;
    transform: rotate(0);
    margin-left: auto;
    margin-top: 0px;
    position: absolute;
    right: 12px;
    top: 50%;

    svg {
      width: 9px;
      height: 9px;
    }

    &--rtt {
      transform: rotate(-180deg);
    }
  }

  &-list {
    list-style: none;
    position: absolute;
    right: 0;
    left: 0;
    top: 100%;
    z-index: 999;
    background: #f8f6f0;
    overflow: hidden;
    border: 1px solid #c2c2c2;
    box-sizing: border-box;
    border-radius: 16px;

    &__item {
      transition: 0.3s;
      padding: 14px 14px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background: #f8f6f0;
      cursor: pointer;
      border-bottom: 1px solid #c2c2c2;

      &:focus,
      &:hover {
        background: #f0f4f8;
        border-radius: 0px 0px 16px 16px;
      }
    }

    &__text {
      font-weight: 500;
      font-size: 16px;
      line-height: 12px;
      color: #999;
    }
  }
}

